.img__container{min-width:300px;overflow:hidden;user-select:none}.img__container:hover .img__tooltipe{opacity:1;transition-property:opacity;transition-duration:.4s;transition-timing-function:ease-in-out}.img__slide{position:relative}.img__tooltipe{position:absolute;content:" ";white-space:nowrap;bottom:50px;left:50%;z-index:10;border-radius:50px;padding:12px 17px;background-color:rgba(255,255,255,.9);box-shadow:0 0 20px rgba(36,36,36,.1019607843);transform:translateX(-50%);opacity:0;transition-property:opacity;transition-duration:.4s;transition-timing-function:ease-in-out}